<template>
  <div class="authentication padding students">
    <!-- 学院管理 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/main/authentication' }">基础信息</el-breadcrumb-item>
        <el-breadcrumb-item>
          学院管理
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="examination-dv">
        <img src="@/assets/mao.png" alt="">
        <span>考核方式管理</span>
    </div>
    <div class="authentication-inp">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="学院搜索：">
          <el-input v-model="formInline.user" placeholder="学院名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" class="btn">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="authentication-btn">
      <el-button type="primary" round @click="close">+ 新建</el-button>
    </div>
    <!-- 弹框组件 -->
    <el-dialog :title="tianjia" :visible.sync="dialogFormVisible" :before-close="handleClose" :close-on-click-modal='false'>
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="学院名称：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="学院编码：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off"></el-input> 
        </el-form-item>
        <el-form-item label="学院简介：" :label-width="formLabelWidth" prop="intro">
          <el-input v-model="form.intro" auto-complete="off"></el-input> 
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="seccons('ruleForm')" v-show="falg1">确 定</el-button>
        <el-button type="primary" @click="seccons1" v-show="falg">确 定</el-button>
      </div>
    </el-dialog>


    <div class="authentication-tab">
      <template>
        <el-table :data="tableData1" border stripe style="width: 100%"  v-loading="loading">
          <el-table-column  :index="indexMethod" label="编号" width="70">
            <template slot-scope="scope">{{scope.$index +1}}</template>
          </el-table-column>
          <el-table-column label="学院名称" width="230">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="学院编号" width="200">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="学院简介" >
            <template slot-scope="scope">
              <span
                style="margin-left: 10px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;"
              >{{ scope.row.intro }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <i class="el-icon-edit"></i>&nbsp;&nbsp;编辑
              </el-button>
              <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <i class="el-icon-delete"></i>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage1"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      falg:false,
      falg1:false,
      currentPage1: 1,
      total: 0,
      pageSize: 15,
      pageNum:1,
      tianjia:'',
       form: {
          name: '',
          code: '',
          intro: '',
        },
      rules:{
        name: [{required: true, message: '学院名称不能为空', trigger: 'blur'}],
        code: [{required: true, message: '学院编号不能为空', trigger: 'blur'}],
        intro: [{required: true, message: '学院简介不能为空', trigger: 'blur'}],
      },
      formInline: {
        user: "",
      },
      tableData: [],
      loading: true,
      tableData1:[]
    };
  },
  methods: {
    onSubmit() {
      // 搜索按钮  // 学院列表
      let keywords = {
          keywords : this.formInline.user,
          rows:this.pageSize,
          page:this.currentPage1
      }
      this.$post('/basic/academy/list',keywords).then(res=>{
        if(res.status == 1){
          this.loading = false
          this.tableData1 = res.data
          this.total = res.total
        }
      })
      // this.formInline.user = ''
    },
    list(){
      let keywords = {
          rows:this.pageSize,
          page:this.currentPage1
      }
      this.$post('/basic/academy/list',keywords).then(res=>{
        if(res.status == 1){
          this.loading = false
          this.tableData1 = res.data
          this.total = res.total
        }
      })
    },
    // 新增列表    
    academyAdd(){
        let name = this.form.name
        let code = this.form.code
        let intro = this.form.intro
        this.$post('/basic/academy/add',{name,code,intro}).then(res=>{
        if(res.status ==1){
            this.$notify({
              message: '新增成功',
              type: 'success'
            });
            this.tableData1.unshift(res.data)
            this.onSubmit()
            }else{
              this.form = res.message
            }
      })
    },
    handleEdit(index, row) {
      // 编辑对话框显示值
      this.tianjia = '编辑学院' 
      this.falg1= false
      this.falg = true
      this.dialogFormVisible = true
      this.form.name = row.name
      this.form.code = row.code
      this.form.intro = row.intro
      this.form.id = row.id
    },
   close(){
      this.tianjia = '添加学院' 
      this.falg1= true
      this.falg = false
      this.dialogFormVisible = true
      this.form.name = ''
      this.form.code = ''
      this.form.intro = ''
    },
    seccons(formName){
      // 确定按钮的点击提交编辑内容
      this.$refs[formName].validate((valid) => {
          if (valid) {
            this.academyAdd()
            this.dialogFormVisible = false
            this.list()
          }  else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    seccons1(){
      // 确定按钮的点击提交编辑内容
      let name = this.form.name
      let code = this.form.code
      let intro = this.form.intro
      let id = this.form.id
      this.$put('/basic/academy/edit',{intro,name,code,id}).then(res=>{
        if(res.status == 1){
          this.form.name = res.data.name
          this.form.code = res.data.code
          this.form.intro = res.data.intro
          this.onSubmit()
        }
      })
        this.dialogFormVisible = false
    },
     resetForm(formName) {
        this.$refs[formName].resetFields();
        this.dialogFormVisible = false
    },
    handleClose(){
       this.$refs['ruleForm'].resetFields();
      this.dialogFormVisible = false
    },
    
    handleDelete(index, row) {
      // 删除列表信息--------删除接口--row对象 /basic/academy/del/{id}
      this.$confirm('确定删除该信息?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            this.$deletes(`/basic/academy/del/${row.id}`).then(res=>{
              if(res.status == 1){
                this.onSubmit()
              }
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        }); 
    },
   
    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.onSubmit()
    },
    handleCurrentChange(val) {
      this.currentPage1 = val;
      this.onSubmit();
    }
  },
  mounted(){
    this.list()
  }
};
</script>
<style lang="scss" scoped>
.authentication {
  .el-button--text{
    padding-left: 13px;
  }
  .authentication-inp {
    // margin-top: 18px;
    float: left;
   
  }
  .authentication-btn {
    margin-top: 9px;
    float: right;
  }
  .authentication-tab{
    background-color: #fff;
    margin-top: 46px;
    padding: 0 15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.078);
  }
  .block {
    float: left;
    width: 100%;
    text-align: right;
    padding: 20px 0px;
    background-color: #fff;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.078);
  }
}
</style>



